import axios from 'axios';
export var instance = axios.create({
    withCredentials: true,
    baseURL: 'http://localhost:3000/',
});
export var authAPI = {
    me: function () {
        return instance.get("auth/me").then(function (res) { return res.data; });
    },
    login: function (email, password) {
        return instance.post("auth/login", { email: email, password: password, }).then(function (res) { return res.data; });
    },
    logout: function () {
        return instance.delete("auth/login").then(function (res) { return res.data; });
    },
};
